@import url(https://fonts.googleapis.com/css?family=Raleway:400,400i,700);
body {
    margin: 0;
    font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Exo', 'Oxygen', 'Ubuntu',
        'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow: auto;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.area {
    margin: 0px;
    padding: 0px;
    width: 100%;
    height: 100vh;
}

.boxes {
    margin: 0px;
    padding: 0px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-image: linear-gradient(to bottom, #ffffff, #ffffff);
}

.boxes .backgroundBox {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(231, 231, 231, 0.8);
    animation: animate 30s linear infinite;
    bottom: -150px;
    pointer-events: none;
}

.boxes .backgroundBox:nth-child(1) {
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}

.boxes .backgroundBox:nth-child(2) {
    left: 10%;
    width: 90px;
    height: 90px;
    animation-delay: 2s;
    animation-duration: 20s;
}

.boxes .backgroundBox:nth-child(3) {
    left: 70%;
    width: 45px;
    height: 45px;
    animation-delay: 4s;
}

.boxes .backgroundBox:nth-child(4) {
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.boxes .backgroundBox:nth-child(5) {
    left: 65%;
    width: 30px;
    height: 30px;
    animation-delay: 0s;
}

.boxes .backgroundBox:nth-child(6) {
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.boxes .backgroundBox:nth-child(7) {
    left: 35%;
    width: 50px;
    height: 50px;
    animation-delay: 7s;
}

.boxes .backgroundBox:nth-child(8) {
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.boxes .backgroundBox:nth-child(9) {
    left: 20%;
    width: 35px;
    height: 35px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.boxes .backgroundBox:nth-child(10) {
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}

.boxes .backgroundBox:nth-child(11) {
    left: 2%;
    width: 25px;
    height: 25px;
    animation-delay: 1.5s;
    animation-duration: 20s;
}

.boxes .backgroundBox:nth-child(12) {
    left: 5%;
    width: 35px;
    height: 35px;
    animation-delay: 1s;
    animation-duration: 40s;
}

@keyframes animate {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100% {
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
}

.home-section {
    padding-bottom: 99%;
    background-color: #aacaea;
    text-align: center;
    display: grid;
    justify-content: center;
    align-items: center;
    overflow: auto;
}

.infoBox {
    margin-top: 30px;
    display: block;
    width: 600px;
    height: 320px;
    position: relative;
    resize: none;
    padding: 10px;
    border-radius: 10px;
    background-color: ghostwhite;
    border: #cfcdcd;
}

.infoBox a {
    font-weight: bold;
}

.downloadPDFButton a {
    background: #ffffff;
    border: solid 1px #9f9c9c;
    border-radius: 15px;
    display: inline-block;
    height: 100px;
    line-height: 100px;
    margin: 5px;
    position: relative;
    text-align: center;
    vertical-align: middle;
    width: 100px;
}

.downloadPDFButton a span {
    background: #f2594b;
    border-radius: 4px;
    color: #ffffff;
    display: inline-block;
    font-size: 11px;
    font-weight: 700;
    line-height: normal;
    padding: 5px 10px;
    position: relative;
    text-transform: uppercase;
    z-index: 1;
}

.downloadPDFButton a span:last-child {
    margin-left: -20px;
}

.downloadPDFButton a:before,
.downloadPDFButton a:after {
    background: #ffffff;
    border: solid 3px #95b8d4;
    border-radius: 4px;
    content: '';
    display: block;
    height: 35px;
    left: 50%;
    margin: -17px 0 0 -12px;
    position: absolute;
    top: 50%;
    /*transform:translate(-50%,-50%);*/

    width: 25px;
}

.downloadPDFButton a:hover:before,
.downloadPDFButton a:hover:after {
    background: #e2e8f0;
}
/*a:before{transform:translate(-30%,-60%);}*/

.downloadPDFButton a:before {
    margin: -23px 0 0 -5px;
}

.downloadPDFButton a:hover {
    background: #e2e8f0;
    border-color: #9fb4cc;
}

.downloadPDFButton a:active {
    background: #dae0e8;
    box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.25);
}

.downloadPDFButton a span:first-child {
    display: none;
}

.downloadPDFButton a:hover span:first-child {
    display: inline-block;
}

.downloadPDFButton a:hover span:last-child {
    display: none;
}

.certification-form textarea {
    margin-top: 30px;
    display: block;
    width: 500px;
    height: 300px;
    position: relative;
    resize: none;
    padding: 10px;
    border-radius: 10px;
}

.certification-form select {
    position: relative;
    cursor: pointer;
    display: inline-block;
    text-align: center;
    margin: 10px 10px 10px auto;
    border-radius: 3px;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
    background-color: #fff;
    padding: 4px;
}

.certification-form input {
    position: relative;
    padding: 6px 30px;
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
    background-color: #ffffff;
}

.certification-form input:active {
    position: relative;
    background-color: #cfcdcd;
}

